import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {NgForm} from '@angular/forms';
import { DecimalPipe } from '@angular/common';
import { PercentPipe } from '@angular/common';
import { MarketService } from '../market.service';

@Component({
  selector: 'app-conversion',
  templateUrl: './conversion.component.html',
  styleUrls: ['./conversion.component.css'],
  providers: [MarketService]
})
export class ConversionComponent implements OnInit {

  constructor(private http: HttpClient, private marketService: MarketService) { }

  public market: any;
  public usdPrice: number;
  public vndPrice: number;
  public amount = 1;
  public buyFee = 0.0025;
  public remitanoPrice = 300000000;
  public profit = 0.5;
  public usdProfit = 0;
  public vndProfit = 0;
  public conversionFee = 0.01;
  public wireToUsaFee = 0.01;
  public pricePaid = 0;
  public conversionRate = 0;
  public url = 'https://api.coindesk.com/v1/bpi/currentprice/vnd.json';
  ngOnInit() {
    this.http.get(this.url).subscribe(data => {
      this.conversionRate = Math.trunc(data['bpi'].VND.rate_float / data['bpi'].USD.rate_float);
    });

    this.marketService.GetBtcPrice().subscribe(res => {
      this.market = res;
      console.log(res);
      this.usdPrice = res.data.amount / 1.01;
      this.vndPrice = this.usdPrice * this.conversionRate;
      this.pricePaid = this.amount * this.usdPrice;
    });
  }

  public Calculate() {
    this.marketService.GetBtcPrice().subscribe(data => {
      this.usdPrice = data.data.amount / 1.01;
      this.pricePaid = this.amount * this.usdPrice;
      this.vndProfit = (this.remitanoPrice - this.amount * this.usdPrice * this.conversionRate
        * (1 + this.conversionFee + this.wireToUsaFee + this.buyFee));
      this.usdProfit = this.vndProfit / this.conversionRate;
      // this.profit = (this.remitanoPrice - this.amount * this.vndPrice * (1 + this.conversionFee + this.wireToUsaFee))
      // / (this.amount * this.vndPrice);
      this.profit = this.usdProfit / this.pricePaid;
      this.vndPrice = this.conversionRate * this.usdPrice;
    });
  }

  public CalculateEth() {
    this.marketService.GetEthPrice().subscribe(data => {
      // Read the result field from the JSON response.
      this.usdPrice = data.data.amount / 1.01;
      this.pricePaid = this.amount * this.usdPrice;
      this.vndProfit = (this.remitanoPrice - this.amount * this.usdPrice * this.conversionRate
        * (1 + this.conversionFee + this.wireToUsaFee + this.buyFee));
      this.usdProfit = this.vndProfit / this.conversionRate;
      // this.profit = (this.remitanoPrice - this.amount * this.vndPrice * (1 + this.conversionFee + this.wireToUsaFee))
      // / (this.amount * this.vndPrice);
      this.profit = this.usdProfit / this.pricePaid;
      this.vndPrice = this.conversionRate * this.usdPrice;
    });
  }
}



