import { Observable } from 'rxjs/Rx';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable()
export class MarketService {

  public url = 'https://api.coinmarketcap.com/v1/ticker/?limit=5';
  public gdaxBtc = 'https://api.coinbase.com/v2/prices/BTC-USD/buy';
  public gdaxEth = 'https://api.coinbase.com/v2/prices/ETH-USD/buy';

  constructor(private http: HttpClient) { }

  GetBtcPrice(): Observable<any> {
    return this.http.get(this.gdaxBtc);
  }

  GetEthPrice(): Observable<any> {
    return this.http.get(this.gdaxEth);
  }
}



